.ld {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}
.ld div {
    position: absolute;
    top: 33px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #0fbcda;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.ld div:nth-child(1) {
    left: 8px;
    animation: ld-1 0.6s infinite;
}
.ld div:nth-child(2) {
    left: 8px;
    animation: ld-2 0.6s infinite;
}
.ld div:nth-child(3) {
    left: 32px;
    animation: ld-2 0.6s infinite;
}
.ld div:nth-child(4) {
    left: 56px;
    animation: ld-3 0.6s infinite;
}
@keyframes lds-1 {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}
@keyframes ld-3 {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}
@keyframes ld-2 {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(24px, 0);
    }
}
